import dataDev from 'static/js/config-dev.js'
import dataPro from 'static/js/config-pro.js'

let globalData = process.env.NODE_ENV === 'production' ? dataPro : dataDev

globalData.browsers = {
  mobile: !!navigator.userAgent.match(/AppleWebKit.*Mobile.*/), //
  android: navigator.userAgent.indexOf('Android') > -1 || navigator.userAgent.indexOf('Adr') > -1 //android
}

export default globalData


